@font-face {
  font-family: "Steagal";
  src: url("assets/Steagal/Steagal.otf") format("opentype");
}
@font-face {
  font-family: "Steagal-Bold";
  src: url("assets/Steagal/Steagal-Medium.otf") format("opentype");
}
@font-face {
  font-family: "Steagal-Italic";
  src: url("assets/Steagal/Steagal-Italic.otf") format("opentype");
}
body {
  -webkit-font-kerning: auto;
          font-kerning: auto;
  font-family: 'Steagal', Arial, Helvetica, sans-serif;
}
h1, h2, h3, h4, h5 {
  font-family: 'Steagal-Bold', Arial, Helvetica, sans-serif;
}
i, .blockquote {
  text-align: center;
  font-size: 1.225em;
  font-family: 'Steagal-Italic'
}

body {
  padding-top: 20px;
  padding-bottom: 40px;
}

/* Custom container */

.container-narrow {
  margin: 0 auto;
  max-width: 700px;
}

.container-narrow>hr {
  margin: 30px 0;
}

/* Main marketing message and sign up button */

.jumbotron {
  margin: 60px 0;
  text-align: center;
}

.jumbotron h1 {
  font-size: 72px;
  line-height: 1;
}

.jumbotron .btn {
  font-size: 21px;
  padding: 14px 24px;
}

/* Supporting marketing content */

.marketing {
  margin: 60px 0;
}

.marketing p+h4 {
  margin-top: 28px;
}

.nav-pills > .active > a {
  background-color: hsl(286, 29%, 26%);
}
.nav-pills > .active > a:hover {
  background-color: hsl(182, 48%, 47%);
}

.nav-pills > a {
  padding: 8px;
  color: hsl(182, 48%, 47%);
}
a {
  text-decoration: none;
}

h2  { 
  font-weight: bold;
  color: hsl(286, 29%, 26%);  
}

h3 {
  font-weight: bold;  
  color: hsl(32, 87%, 53%);
}


